#chartBox{
    position:fixed;
    z-index: 100;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    color: white;
}
#chartCloser{
    position: absolute;
    top: 30px;
    right: 30px;
    color: white;
    font-size: 2em;
}
#chartTitle{
    width: 800px;
    padding: 10px;
    color: rgb(255, 208, 0);
    background-color: rgba(0, 0, 0, 0.8);
}
#chartTimeframe{
    width: 800px;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.8);
}